.hero {
  background-image: url("../../../../public/solo1.jpg");
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 98vh;
  padding-top: 20%;
  padding-right: 50px;
  color: #ffffff;
  opacity: 0.7;


}

.hero #heading h3 {
  color: whitesmoke;
  font-size: x-large;
}

.primary {
  background-color: rgb(109, 93, 151);
  color: whitesmoke;
  cursor: pointer;
}

button {
  color: rgb(82, 82, 128);
  cursor: pointer;
}


.margin {
  margin-left: 40px;
  height: 20vh;
  width: 50vh;
  display: flex;
  justify-content: space-around;
  text-align: center;
  align-content: center;
  margin-top: 30%;
}

.marginTop {
  /* background-color: #ffffff; */
}

.marginTop .started {
  background-color: rgb(77, 77, 129);
  text-decoration: "none";
  border-radius: 10px;
  box-shadow: 0.5px 0.5px 5px 4px rgb(255, 255, 255);

}

.margin a {
  background-color: rgb(89, 89, 145);
  color: whitesmoke;
  border-radius: 6px;
  margin-left: 20px;
  height: 40%;
  width: 50%;
  padding-top: 10px;
}

.marginTop {
  position: absolute;
  top: 250px;
}

.marginTop .started span {
  font-size: 30px;
  color: gold;
  font-family: cursive;

}

.marginTop .started_1  {
  
  background-color: rgb(77, 77, 129);
  text-decoration: "none";
  border-radius: 10px;
  box-shadow: 0.5px 0.5px 5px 4px rgb(255, 255, 255);
}

.marginTop .started_1 span {
  font-size: 30px;
  color: gold;
  font-family: cursive;

}



.marginTop .started {
  padding: 20px;
  margin-left: 210px;

}


.marginTop .started_1 {
  padding: 20px;
  margin-right: 20px;
  margin-left: 10px;

}

.margin .started {
  color: rgb(90, 90, 134);
}

.hero #heading h3 {
  animation: slide-in 7s ease-out both;
  white-space: nowrap;
  overflow: hidden;
  width: 0%;

}

@keyframes slide-in {
  from {
    width: 0%;
  }

  to {
    width: 100%;
  }
}



.hero #heading {
  text-align: left;
  margin: 0;
  padding: 0;
  width: 110%;

}

@media screen and (max-width: 768px) {
  .margin {
    height: 40vh;
  }

  .hero {
    background-position: right;
    padding-top: 50%;
    height: 80vh;
    background-color: blue;
  }

  .hero .row {
    width: 100%;
  }
}


