.testimonial {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  margin-top: 20px;
}

.testimonial .testimonial-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  border-radius: 8px;
  background-color: #f0f2f5;
}

.testimonial .testimonial-item .testimonial-img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.testimonial .testimonial-item .testimonial-content {
  text-align: center;
}

.testimonial .testimonial-item .testimonial-content .testimonial-name {
  font-weight: bold;
  margin-bottom: 5px;
}

.testimonial .testimonial-item .testimonial-content .testimonial-text {
  color: #555555;
}

.testimonial .testimonial-item:hover {
  background-color: #e9ebee;
  cursor: pointer;
}
